ul.aboutus {
    list-style: none;
    padding: 0;
  }
  .aboutus li {
    padding-left: 1.3em;
    margin-bottom: 0.3rem;
    line-height: 1.6;
  }
  .aboutus li:before {
    content: "\f054"; /* FontAwesome Unicode */
    font-family: FontAwesome;
    display: inline-block;
    font-weight: 300;
    color:var(--theme-color);
    margin-left: -1.3em; /* same as padding-left set on li */
    width: 1.3em; /* same as padding-left set on li */
    position: relative;
    top: 2px;
  }
  .rgba-blueish-dark{
      background-color:rgba(36, 36, 49, 0.9)
  }
  .register-section:before{
      content:"";
      position: absolute;
      left: 0; right: 0;
  top: 0; bottom: 0;
    background-color:rgba(36, 36, 49, 0.9);
  }
